import React, { Component } from 'react'
import { Layout,Card,Table,notification } from 'antd';

import {
  tennisSlot
  } from "../../../../actions/userActions";

const { Content } = Layout;

class SlotBooking extends Component {

    state = {

        tennisData : [],
        cycleData : [],
        swimmingData : [],
        badmintonData : [],
        gymData : [],
        clubData : [],

    }

    componentDidMount() {
        if (JSON.parse(localStorage.getItem("user"))) {
            console.log(JSON.parse(localStorage.getItem("user")).user);
            // this.props.history.push("/dashboard/card/play");
            notification.success({
              message: "Dashboard opened Successfully",
              placement: "bottomRight",
            });
            this._getUserInfo();
          }
          else{
            // this.props.history.push("/dashboard/card/manage");
            notification.error({
              message: "User Dashboard couldn't be opened",
              placement: "bottomRight",
            });
          }
  
    }

    _getUserInfo = () => {
      let tennisUsers = tennisSlot(
        "tennis"
      );
      tennisUsers.then((res, err) => {
        if (err) console.log(err);
        else {
          console.log(res);
          this.setState(
            { 
              tennisData : res.users,
             },
          );
        }
      });

      let cycleUsers = tennisSlot(

        "cycle"
      );
      cycleUsers.then((res, err) => {
        if (err) console.log(err);
        else {
          console.log(res);
          this.setState(
            { 
              cycleData : res.users,
             },
          );
        }
      });

      let swimmingUsers = tennisSlot(
        "swimming"
      );
      swimmingUsers.then((res, err) => {
        if (err) console.log(err);
        else {
          console.log(res);
          this.setState(
            { 
              swimmingData : res.users,
             },
          );
        }
      });

      let badmintonUsers = tennisSlot(
        "badminton"
      );
      badmintonUsers.then((res, err) => {
        if (err) console.log(err);
        else {
          console.log(res);
          this.setState(
            { 
              badmintonData : res.users,
             },
          );
        }
      });

      let gymUsers = tennisSlot(
        "gym"
      );
      gymUsers.then((res, err) => {
        if (err) console.log(err);
        else {
          console.log(res);
          this.setState(
            { 
              gymData : res.users,
             },
          );
        }
      });

      let clubUsers = tennisSlot(

        "club"
      );
      clubUsers.then((res, err) => {
        if (err) console.log(err);
        else {
          console.log(res);
          this.setState(
            { 
              clubData : res.users,
             },
          );
        }
      });
    };

    render() {

        const columns = [
            {
              title: 'Name',
              dataIndex: 'name',
              key: 'name',
            },
            {
              title: 'Date',
              dataIndex: 'slotDate',
              key: 'slotDate',
            },
          ];

        return (
            <Layout>
                <Content className='content'>

                    <div className="row" >

                      <div className="col-md-6">
                        <Card title="Tennis Court" >
                            <Table dataSource={this.state.tennisData} columns={columns} />
                        </Card>
                      </div>

                      <div className="col-md-6">
                        <Card title="Cycle Tracks" >
                            <Table dataSource={this.state.cycleData} columns={columns} />
                        </Card>
                      </div>  
                    </div>

                    <div className="row" style={{ marginTop : 10 }}>

                      <div className="col-md-6">
                        <Card title="Swimming Pool" >
                            <Table dataSource={this.state.swimmingData} columns={columns} />
                        </Card>
                      </div>

                      <div className="col-md-6">
                        <Card title="Badminton Court" >
                            <Table dataSource={this.state.badmintonData} columns={columns} />
                        </Card>
                      </div>  
                    </div>

                    <div className="row" style={{ marginTop : 10,marginBottom: 20 }}>

                      <div className="col-md-6">
                        <Card title="Gym" >
                            <Table dataSource={this.state.gymData} columns={columns} />
                        </Card>
                      </div>

                      <div className="col-md-6">
                        <Card title="Club House" >
                            <Table dataSource={this.state.clubData} columns={columns} />
                        </Card>
                      </div>  
                    </div>
                </Content>
            </Layout>
        )
    }
}

export default SlotBooking;