import React, { Component } from "react";
import {
  Route,
  BrowserRouter as Router,
  Switch,
  Redirect,
} from "react-router-dom";
import DashboardManage from './scoreCard'
import SlotBooking from './playGame'

class Routes extends Component {
  render() {
    return (
      <Switch>
        <Route path="/dashboard/card/manage" component={SlotBooking} />
        <Route path="/dashboard/card/play" component={DashboardManage} />
        <Redirect from='/dashboard/card' to='/dashboard/card/play'/>
      </Switch>
    );
  }
}

export default Routes;
