const { Content } = Layout;
const { Meta } = Card;


class DashboardManage extends Component {

    componentDidMount() {
        if (JSON.parse(localStorage.getItem("user"))) {
          console.log(JSON.parse(localStorage.getItem("user")).user);
          // this.props.history.push("/dashboard/card/manage");
          notification.success({
            message: "Let's Play the Game",
            placement: "bottomRight",
          });
        }
        else{
          // this.props.history.push("/dashboard/card/play");
          notification.error({
            message: "Somthing went Wrong",
            placement: "bottomRight",
          });
        }
      }


      buildCards = () => {
        let id = 0
        const images = {angular, css, go, html, rail, react, scala, vue}
        const cards = Object.keys(images).reduce((result, item) => {
          const getCard = () => ({
            id: id++,
            type: item,
            backImg,
            frontImg: images[item],
            flipped: false,
          })
          return [...result, getCard(), getCard()]
        }, [])
        return this.suffle(cards)
      }
      
      suffle = (arr) => {
        let len = arr.length
        for (let i = 0; i < len; i++) {
          let randomIdx = Math.floor(Math.random() * len)
          let copyCurrent = {...arr[i]}
          let copyRandom = {...arr[randomIdx]}
          arr[i] = copyRandom
          arr[randomIdx] = copyCurrent
        }
        return arr
      }
      



    render() {

      const cards = this.buildCards()
        
        return (
          
            <Layout>
                <Content className='content'>
                  <div style={{marginLeft: "25%" }}>
                    <Board cards={cards} />
                  </div>
                     
                </Content>
            </Layout>
        
        )
    }
}

export default DashboardManage;